import addressPicker from './packages/addressPicker/'
import countUp from './packages/countUp/'
import datetime from './packages/datetime/'
import dialog from './packages/dialog/'
import loading from './packages/loading/'
import selectPicker from './packages/selectPicker/'
import swiper from './packages/swiper/'
import vswitch from './packages/switch/'
import lottery from './packages/lottery/'
import dropDown from './packages/dropDown/'
import iverPlugin from './plugin/all/'


// css
import './scss/index.scss';

const components = [
	addressPicker,
	countUp,
	datetime,
	selectPicker,
	swiper,
	vswitch,
	lottery,
	dropDown
];



const iver = function(Vue,opts = {}) {

	components.forEach(component => {
   		Vue.component(component.name, component);
 	});

	Vue.use(iverPlugin)

}



export {
	iver,
	addressPicker,
	countUp,
	datetime,
	dialog,
	loading,
	selectPicker,
	swiper,
	vswitch,
	dropDown,
	iverPlugin,
}



