import loading from '../../packages/loading';
import dialog from '../../packages/dialog';


var iver = {};

iver.install = function (Vue, options) {

	Vue.$iver = {
		loading : loading,
		dialog : dialog
	};
  	//
  	Vue.mixin({
    	created: function () {
      		this.$iver = Vue.$iver
    	}
  	})

}

export default iver;


